export default {
	props:{
		item:{
			type:Object,
			"default":{}
		}
	},
	data() {
		return {
			orientationList: ['东', '南', '西', '北'],
		};
	},
	methods:{
		showDetail(item){
			this.$emit('emitClick',item)
		},
	}
}
